body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}


.input-data {
  display: flex;
  margin-bottom: 30px;
}

.title {
  font-weight: bold;
  min-width: 180px;
}

.input-data li{
  display: flex;
}

.input-data li label{
  width: 24px;
  margin:3px;
}

.input-data li label input{
  width: 32px;
  height: 16px;
  margin-left: -24px;
}

.input-data li button{
  width: 22px;
  height: 22px;
  margin:3px;
  border-radius: 6px;
  background-color: #777;
  color: #fff;
}
	
	
